#nav-var.vertical > #links.vertical >li  > .link {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 15px 40px;
    gap: 20px;

    border-radius: 8px;

    color: white;

    transition: background-color 1s;
}

#nav-var.vertical > #links.vertical > li > .link.active {
    background-color: var(--secondary);
}
